//控制组件
import React from 'react';
import ReactDOM  from 'react-dom';

class ControllerUnit extends React.Component{
	constructor(){
		super();
		this.handleClcik=this.handleClcik.bind(this);
	}

	//点击处理函数
	handleClcik(e){
		//如果点击的是当前在选中的态的按钮，则翻转图片，否则将对应的图片居中
		this.props.inverse();
		e.preventDefault();  //阻止触发默认事件
		e.stopPropagation(); //阻止事件冒泡
	}

	render(){
		var controllerUnitClassName="controller-unit";
		if(this.props.arrange.isCenter){
			controllerUnitClassName+=" is-center";    /**设置按钮为居中态**/
			if(this.props.arrange.isInverse){
				controllerUnitClassName+=" is-inverse";  /**设置翻转***/
			}
		}
		return (
			<span className={controllerUnitClassName} onClick={this.handleClcik}></span>
		);
	}
}

export default ControllerUnit;


