// 单独的图片显示组件
import React from 'react';
import ReactDOM from 'react-dom';
class ImgFigure extends React.Component{
	constructor(props){
		super(props);
		this.clickHandler=this.clickHandler.bind(this);
	}

	//点击函数
	clickHandler(e){
		this.props.inverse(); //执行父组件传递的函数
		e.stopPropagation();  //阻止冒泡
		e.preventDefault();   //阻止默认事件
	}

	render(){
		var styleObj={};
		//console.log(this.props.arrange);
		//如果props属性中指定了图片的位置，则使用
		if(this.props.arrange && this.props.arrange.pos){
			var pos=this.props.arrange.pos;
			styleObj={
				left:pos.left,
				top:pos.top,
				zIndex:11
			};
		}

		//翻转相关
		var imgFigureClassName="img-figure";
		if(this.props.arrange){
			imgFigureClassName+=this.props.arrange.isInverse ? ' is-inverse' : '';
		}
		

		//添加旋转
		if(this.props.arrange && this.props.arrange.rotate){
			/***
			['MozTransform','msTransform','WebkitTranfrom','transform'].forEach((value)=>{
				styleObj[value]='rotate('+this.props.arrange.rotate+'deg)';
			});
			**/

			if(typeof(styleObj.transform)=='undefined'){
				styleObj['transform']='rotate('+this.props.arrange.rotate+'deg)';
			}
		}


		//居中 最上层显示
		if(this.props.arrange.isCenter){
			styleObj.zIndex=101;
		}
		
		return (
			<figure className={imgFigureClassName} style={styleObj} onClick={this.clickHandler}>
				<img src={`./imgs/${this.props.data.imgName}`} alt={this.props.data.title} />
				<figcaption>
					<h2 className="img-title">{this.props.data.title}</h2>
					<div className="img-back" onClick={this.clickHandler}>
						<p>{this.props.data.desc}</p>
					</div>
				</figcaption>
			</figure>
		);
	}
}

export default ImgFigure;


